import { FC } from "react";
import classNames from "classnames/bind";

import { Text } from "common/components/Text";
import { Icon } from "common/components/Icons";
import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import Man from "common/components/Icons/icons/64/man.svg";
import Food from "common/components/Icons/icons/64/food.svg";
import Feather from "common/components/Icons/icons/64/feather.svg";

import { AdvantageType, AdvantagesListProps } from "./AdvantagesList.types";
import styles from "./AdvantagesList.module.scss";

const cx = classNames.bind(styles);

const renderIcon = (iconName?: string) => {
    switch (iconName) {
        case "man64":
            return <Man />;
        case "food64":
            return <Food />;
        case "feather64":
            return <Feather />;
        default:
            return null;
    }
};

export const AdvantagesList: FC<AdvantagesListProps> = ({ list = [] }) => {
    return (
        <ul className={cx("root")}>
            {list.map((item: AdvantageType) => {
                const { title, description } = item;
                return (
                    <div key={title} className={cx("item")}>
                        <Icon className={cx("icon")}>
                            {renderIcon(item.icon)}
                        </Icon>
                        <Heading
                            className={cx("title")}
                            tag={HeadingTag.H3}
                            type={HeadingType.M}
                        >
                            {title}
                        </Heading>
                        <Text className={cx("description")}>{description}</Text>
                    </div>
                );
            })}
        </ul>
    );
};
