import { FC } from "react";
import classNames from "classnames/bind";

import { CountryVersion, Theme } from "common/constants/enum";
import { Container } from "common/components/Container";
import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { Text, TextTag } from "common/components/Text";
import { Button, ButtonSize } from "common/components/new/Button";
import { Picture } from "common/components/Picture";
import { HeaderShort } from "common/components/new/HeaderShort";

import { HeadProps } from "./Head.types";
import styles from "./Head.module.scss";

const cx = classNames.bind(styles);

export const Head: FC<HeadProps> = ({
    title,
    textRedTitle,
    images,
    subtitle,
    text,
    buttonText,
    contacts,
    theme = Theme.DARK,
    onScrollToAppointment,
    isFixedButtonActive,
}) => (
    <section className={cx("root")}>
        <HeaderShort
            {...contacts}
            version={CountryVersion.GERMANY}
            button={{
                name: buttonText,
                onClick: onScrollToAppointment,
                className: cx("button-fixed"),
            }}
            theme={theme}
            isFixedButtonActive={isFixedButtonActive}
            className={cx("header")}
        />
        <Container>
            <Heading
                className={cx("title")}
                tag={HeadingTag.H1}
                type={HeadingType.XL}
            >
                {title}&nbsp;
                <Text tag={TextTag.Span} className={cx("title__text-red-box")}>
                    {textRedTitle.map((textRed) => (
                        <span className={cx("title__text-red")} key={textRed}>
                            {textRed}
                        </span>
                    ))}
                </Text>
            </Heading>
            <div className={cx("container")}>
                <div className={cx("container-images")}>
                    {images.map((image, index) => (
                        <Picture
                            classNameImg={cx("image", `image_${index + 1}`)}
                            width={257}
                            height={347}
                            key={index}
                            {...image}
                        />
                    ))}
                </div>
                <div className={cx("container-text")}>
                    <Heading className={cx("subtitle")} type={HeadingType.M}>
                        {subtitle}
                    </Heading>
                    <Text className={cx("text")}>{text}</Text>
                    <Button
                        size={ButtonSize.M}
                        className={cx("button")}
                        onClick={onScrollToAppointment}
                        version={CountryVersion.GERMANY}
                    >
                        {buttonText}
                    </Button>
                </div>
            </div>
        </Container>
    </section>
);
