import { FC } from "react";
import classNames from "classnames/bind";

import { Text } from "common/components/Text";
import { Heading, HeadingTag } from "common/components/new/Heading";

import styles from "./MainContent.module.scss";
import { PriceBlockMainContentProps as Props } from "./MainContent.types";

const cx = classNames.bind(styles);

const PriceBlockMainContent: FC<Props> = ({ title, subtitle, description }) => (
    <div
        className={cx("content", {
            "content_with-teeth": description,
        })}
    >
        {subtitle && (
            <div className={cx("subtitle")}>
                <Text className={cx("subtitle__text")}>{subtitle}</Text>
            </div>
        )}
        <div className={cx("title")}>
            <Heading className={cx("title__text")} tag={HeadingTag.H2}>
                {title}
            </Heading>
        </div>
    </div>
);

export default PriceBlockMainContent;
