import { FC } from "react";
import classNames from "classnames/bind";

import { StagesHowItWorksProps } from "../../HowItWorks.types";
import { StageImageElement } from "../StageImageElement";
import styles from "./StagesImages.module.scss";

const cx = classNames.bind(styles);

export const StagesImages: FC<StagesHowItWorksProps> = ({
    stages,
    imagePosition,
    className,
}) => (
    <div className={cx(className, "root")}>
        {stages.map((stage, index) => (
            <StageImageElement
                key={index}
                imagePosition={imagePosition}
                {...stage}
            />
        ))}
    </div>
);
