import { FC } from "react";
import classNames from "classnames/bind";

import { CountryVersion, PageTheme } from "common/constants/enum";
import { Container } from "common/components/Container";
import { Text, TextTag } from "common/components/Text";
import { Button, ButtonSize } from "common/components/new/Button";
import { Tooltip } from "common/components/Tooltip";
import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";

import MainContent from "./components/MainContent/MainContent";
import { PaymentOnce } from "./components/PaymentOnce";
import { PaymentMonthly } from "./components/PaymentMonthly";
import { PriceBlockProps } from "./PriceBlock.types";
import styles from "./PriceBlock.module.scss";

const cx = classNames.bind(styles);

export const PriceBlock: FC<PriceBlockProps> = ({
    fromText,
    month,
    period,
    button,
    currency,
    duration,
    version,
    className,
    buttonLink,
    prePayment,
    treatments,
    legalSorteo,
    payTypeOnce,
    offerRegular,
    offerExtended,
    onButtonClick,
    payTypeMonthly,
    monthlyPaymentHelperText,
    isMobileButtonHide,
    durationDescription,
    progressTitle,
    paymentComment1,
    paymentComment2,
}) => {
    const buttonAction = onButtonClick
        ? { onClick: onButtonClick }
        : { href: buttonLink };

    return (
        <div className={cx(className, "root")}>
            <Container>
                <div className={cx("content")}>
                    <div
                        className={cx(
                            "merged-column",
                            "treatment",
                            "treatment_with-divider"
                        )}
                    >
                        <Heading
                            tag={HeadingTag.H3}
                            type={HeadingType.M}
                            className={cx("treatment__title", "only-desktop")}
                        >
                            {treatments}
                        </Heading>
                        <div
                            className={cx("button-container", "only-desktop", {
                                "button-container_hide-mobile": isMobileButtonHide,
                            })}
                        >
                            <Button
                                className={cx("button")}
                                size={ButtonSize.M}
                                version={CountryVersion.GERMANY}
                                {...buttonAction}
                            >
                                {button}
                            </Button>
                        </div>
                    </div>
                    <div
                        className={cx(
                            "column",
                            "column_with-divider",
                            "main-content"
                        )}
                    >
                        <MainContent {...offerRegular} />
                    </div>
                    <div className={cx("column", "column_extended")}>
                        <MainContent {...offerExtended} />
                    </div>
                    {offerRegular.description && (
                        <Text
                            className={cx(
                                "column",
                                "column_regular",
                                "column_with-divider",
                                "description",
                                "only-desktop"
                            )}
                        >
                            {offerRegular.description}
                        </Text>
                    )}
                    {offerExtended.description && (
                        <Text
                            className={cx(
                                "column",
                                "column_extended",
                                "description",
                                "only-desktop"
                            )}
                        >
                            {offerExtended.description}
                        </Text>
                    )}
                    {progressTitle && (
                        <Text
                            className={cx(
                                "merged-column",
                                "progress-title",
                                "only-mobile"
                            )}
                        >
                            {progressTitle}
                        </Text>
                    )}
                    <div
                        className={cx(
                            "duration",
                            "merged-column",
                            "only-desktop"
                        )}
                    >
                        <Heading
                            type={HeadingType.M}
                            tag={HeadingTag.H3}
                            className={cx("duration__title")}
                        >
                            {duration}
                        </Heading>
                        <Text className={cx("duration__description")}>
                            {durationDescription}
                        </Text>
                    </div>
                    <div
                        className={cx(
                            "column",
                            "duration-value",
                            "column_with-divider"
                        )}
                    >
                        <span
                            className={cx("only-mobile")}
                            dangerouslySetInnerHTML={{
                                __html: offerRegular.durationMobile,
                            }}
                        ></span>
                        <span className={cx("only-desktop")}>
                            {offerRegular.duration}
                        </span>
                    </div>
                    <div
                        className={cx(
                            "column",
                            "column_extended",
                            "duration-value"
                        )}
                    >
                        <span
                            className={cx("only-mobile")}
                            dangerouslySetInnerHTML={{
                                __html: offerExtended.durationMobile,
                            }}
                        ></span>
                        <span className={cx("only-desktop")}>
                            {offerExtended.duration}
                        </span>
                    </div>
                    <div
                        className={cx("merged-column", "payment", {
                            "payment_with-teeth": offerRegular.description,
                        })}
                    >
                        <Heading
                            tag={HeadingTag.H3}
                            type={HeadingType.M}
                            className={cx(
                                "payment",
                                "payment_monthly",
                                "only-desktop"
                            )}
                        >
                            {payTypeMonthly}
                        </Heading>
                        <Text
                            tag={TextTag.Span}
                            className={cx(
                                "payment",
                                "payment_monthly",
                                "only-mobile"
                            )}
                        >
                            {payTypeMonthly}
                            {monthlyPaymentHelperText && (
                                <Tooltip
                                    className={cx("helper", "only-mobile")}
                                    theme={PageTheme.BLUE}
                                >
                                    <Text
                                        tag={TextTag.Span}
                                        className={cx("helper__content")}
                                    >
                                        {monthlyPaymentHelperText}
                                    </Text>
                                </Tooltip>
                            )}
                        </Text>
                        {legalSorteo && (
                            <Text
                                className={cx(
                                    "merged-column",
                                    "legal",
                                    "legal_sorteo",
                                    {
                                        "legal_with-teeth":
                                            offerRegular.description,
                                    }
                                )}
                            >
                                {legalSorteo}
                            </Text>
                        )}
                    </div>
                    <PaymentMonthly
                        className={cx(
                            "column",
                            "column_with-divider",
                            "column__payment-monthly"
                        )}
                        payment={offerRegular.paymentMonthly}
                        fromText={fromText}
                        month={month}
                        period={period}
                        version={version}
                        currency={currency}
                        prePayment={prePayment}
                    />
                    <PaymentMonthly
                        className={cx(
                            "column",
                            "column_extended",
                            "column__payment-monthly"
                        )}
                        payment={offerExtended.paymentMonthly}
                        fromText={fromText}
                        month={month}
                        period={period}
                        version={version}
                        currency={currency}
                        prePayment={prePayment}
                    />
                    <Heading
                        tag={HeadingTag.H3}
                        type={HeadingType.M}
                        className={cx(
                            "merged-column",
                            "payment",
                            "payment_once",
                            "only-desktop",
                            {
                                "payment_once-with-teeth":
                                    offerRegular.description,
                            }
                        )}
                    >
                        {payTypeOnce}
                    </Heading>

                    <PaymentOnce
                        className={cx(
                            "column",
                            "column_with-divider",
                            "column__payment-once"
                        )}
                        fromText={fromText}
                        payment={offerRegular.paymentOnce}
                        currency={currency}
                        version={version}
                    />

                    <PaymentOnce
                        className={cx(
                            "column",
                            "column_extended",
                            "column__payment-once"
                        )}
                        fromText={fromText}
                        payment={offerExtended.paymentOnce}
                        currency={currency}
                        version={version}
                    />

                    {(paymentComment1 || paymentComment2) && (
                        <div className={cx("prices-footer", "merged-column")}>
                            {paymentComment1 && (
                                <div className={cx("first-payment-comment")}>
                                    <Text>{paymentComment1}</Text>
                                </div>
                            )}
                            {paymentComment2 && (
                                <div className={cx("second-payment-comment")}>
                                    <Text>{paymentComment2}</Text>
                                </div>
                            )}
                        </div>
                    )}
                </div>
            </Container>
        </div>
    );
};
