import { FC } from "react";
import classNames from "classnames/bind";

import { OldPrice } from "common/components/OldPrice";
import { Text } from "common/components/Text";
import { priceFormatter } from "common/utils/priceFormatter";

import { PaymentMonthlyProps } from "./PaymentMontly.types";
import styles from "./PaymentMonthly.module.scss";

const cx = classNames.bind(styles);

export const PaymentMonthly: FC<PaymentMonthlyProps> = ({
    fromText,
    month,
    period,
    payment,
    version,
    currency,
    className,
    prePayment,
}) => {
    const { oldPrice, amount } = payment;

    return (
        <div className={cx(className, "payment-monthly")}>
            {oldPrice && (
                <OldPrice
                    content={priceFormatter(oldPrice, currency, version)}
                />
            )}
            <div className={cx("payment-monthly__row")}>
                <Text className={cx("payment-monthly__from-text")}>
                    {fromText}
                </Text>
                <Text className={cx("payment-monthly__amount")}>{amount}</Text>
                <span className={cx("payment-monthly__currency")}>
                    <Text className={cx("payment-monthly__month")}>
                        {month}
                    </Text>
                    <Text className={cx("payment-monthly__period")}>
                        {period}
                    </Text>
                </span>
            </div>
            {prePayment && (
                <Text className={cx("only-desktop")}>{prePayment}</Text>
            )}
        </div>
    );
};
