import { FC } from "react";
import classNames from "classnames/bind";

import { OldPrice } from "common/components/OldPrice";
import { Heading, HeadingTag } from "common/components/new/Heading";
import { Text } from "common/components/Text";
import { priceFormatter } from "common/utils/priceFormatter";

import { PaymentOnceProps } from "./PaymentOnce.types";
import styles from "./PaymentOnce.module.scss";

const cx = classNames.bind(styles);

export const PaymentOnce: FC<PaymentOnceProps> = ({
    version,
    payment,
    currency,
    fromText,
    className,
}) => {
    const { oldPrice, amount } = payment;

    return (
        <div className={cx(className)}>
            <div className={cx("content")}>
                {oldPrice && (
                    <OldPrice
                        content={priceFormatter(oldPrice, currency, version)}
                    />
                )}
                <Text className={cx("payment-once__from-text")}>
                    {fromText}
                </Text>
                <Heading className={cx("price")} tag={HeadingTag.H3}>
                    {priceFormatter(amount, currency, version)}
                </Heading>
            </div>
        </div>
    );
};
