import { FC } from "react";
import classNames from "classnames/bind";

import { CountryVersion } from "common/constants/enum";
import { Text } from "common/components/Text";
import { Video } from "common/components/Video";
import { Button, ButtonSize } from "common/components/new/Button";
import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { Container } from "common/components/Container";

import { AdvantagesList } from "./components/AdvantagesList";
import { AdvantagesProps } from "./Advantages.types";
import styles from "./Advantages.module.scss";

const cx = classNames.bind(styles);

export const Advantages: FC<AdvantagesProps> = ({
    list,
    title,
    button,
    subtitle,
    linkVideo,
    className,
    description,
    invisibleTitle,
    onButtonClick,
}) => (
    <section className={cx("root", className)}>
        <Container>
            <div className={cx("header")}>
                {linkVideo && (
                    <Video
                        className={cx("video")}
                        autoPlay={true}
                        loop={true}
                        muted={true}
                        playsInline={true}
                        src={linkVideo}
                    />
                )}

                <div className={cx("content")}>
                    <Heading
                        className={cx("title")}
                        tag={HeadingTag.H2}
                        type={HeadingType.XL}
                    >
                        <span className={cx("title_invisible")}>
                            {invisibleTitle}
                        </span>
                        {title}
                    </Heading>

                    <Heading tag={HeadingTag.H2} type={HeadingType.XL}>
                        {subtitle}
                    </Heading>
                    <Text className={cx("description")}>{description}</Text>
                    <Button
                        className={cx("button")}
                        size={ButtonSize.M}
                        onClick={onButtonClick}
                        version={CountryVersion.GERMANY}
                    >
                        {button}
                    </Button>
                </div>
            </div>

            <AdvantagesList list={list} />
        </Container>
    </section>
);
