import { HTMLAttributes } from "react";

import { PictureProps } from "common/components/Picture";

export interface HowItWorksProps extends StagesHowItWorksProps {
    title: string;
}

export interface StagesHowItWorksProps {
    stages: StageHowItWorksProps[];

    imagePosition?: ImagePositionTypes;
    className?: string;
}

export interface StageHowItWorksProps {
    title: string;
    description: string;
    imageLeft: PictureProps;
    imageRight: PictureProps;
    progressDuration: number;
    isActive: boolean;

    imagePosition?: ImagePositionTypes;
}

export enum ImagePositionTypes {
    IS_LEFT = "is-left",
    IS_RIGHT = "is-right",
}

export type HowItWorksContentProps = HowItWorksProps &
    HTMLAttributes<Record<string, never>>;
