import { FC } from "react";
import classNames from "classnames/bind";

import { Picture } from "common/components/Picture";

import {
    ImagePositionTypes,
    StageHowItWorksProps,
} from "../../HowItWorks.types";
import styles from "./StageImageElement.module.scss";

const cx = classNames.bind(styles);

export const StageImageElement: FC<StageHowItWorksProps> = ({
    imageLeft,
    imageRight,
    isActive,
    imagePosition = ImagePositionTypes.IS_LEFT,
}) => {
    const image =
        imagePosition === ImagePositionTypes.IS_RIGHT
            ? { ...imageRight }
            : { ...imageLeft };

    return (
        <div className={cx("root", { "root_is-active": isActive })}>
            <div
                className={cx(
                    "image-content",
                    `image-content_${imagePosition}`
                )}
            >
                <Picture
                    classNameImg={cx("image", {
                        "image_is-active": isActive,
                    })}
                    {...image}
                />
            </div>
        </div>
    );
};
